* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:after,
*:before {
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  line-height: 2.4rem;
}
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-size: 1.6rem;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  background: #fff;
  color: #646464;
}
#root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}
accordion-header h3,
accordion-header span,
svg,
img {
  user-select: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
p {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
  display: inline-block;
}
input {
  font-family: inherit;
  font-size: inherit;
  border: none;
  outline: none;
  overflow-x: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
/* CSS */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

button {
  border: none;
  outline: none;
  background: transparent;
  font-family: inherit;
  cursor: pointer;
}
input,
textarea {
  font-family: inherit;
}
/* Some Extra for pre style */
.two_columns {
  display: flex;
  justify-content: space-between;
}
.two_columns div {
  flex: 0 0 calc(50% - 15px);
}
