/* st global input css  */
textarea,
input {
  color: #667085;
  font-size: 1.6rem;
  font-weight: 400;
  border-radius: 0.8rem;
  border: 0.1rem solid var(--gray-300, #d0d5dd);
  background: var(--White, #fff);
  box-shadow: 0 0.1rem 0.2rem 0 rgba(16, 24, 40, 0.05);
  padding: 1rem 1.4rem;
  width: 100%;
  resize: none;
}
textarea:focus,
input:focus:focus-visible {
  /* border: none; */
  box-shadow: none;
  outline: none;
}

select,
option {
  color: #667085;
  font-size: 1.6rem;
  font-weight: 400;
  border-radius: 0.8rem;
  border: 0.1rem solid var(--gray-300, #d0d5dd);
  background: transparent;
  box-shadow: 0 0.1rem 0.2rem 0 rgba(16, 24, 40, 0.05);
  padding: 1rem 1.4rem;
  width: 100%;
}

input[type="radio"] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #333;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
  position: relative;
  margin-right: 10px;
}
/* form_radio */
.form_radio {
  display: block;
  margin-bottom: 10px;
}
/* Customize the radio button when checked */
.radio-control {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
input[type="radio"]:checked + .radio-control {
  border-color: #2d2d2d;
}

input[type="radio"]:checked + .radio-control span {
  background-color: #2d2d2d;
  width: 50%;
  height: 50%;
  display: block;
  border-radius: 50px;
}

/* Hide the default radio button */
input[type="radio"] {
  display: none;
}

/* Style the radio label */
.custom-radio {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 16px;
  color: #333;
  user-select: none;
  margin: 0;
}
.custom-radio {
  display: block flex !important;
  align-items: center;
}
.custom-radio:hover {
  color: #000;
}
/* Style the custom radio control */
.radio-control {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  margin-right: 8px;
  transition: background-color 0.2s, border-color 0.2s;
  border: 1px solid #878787;
  background: #fff;
}
#signup_references .custom-radio {
  font-size: 14px;
}
/* check box plan css  */
.plan {
  cursor: pointer;
  width: inherit;
  min-width: inherit;
  margin: 5px !important;
  width: 100%;
}
.plan input[type="checkbox"] {
  width: 100%;
  padding: 9px 10px;
  border-radius: 6px;
  border: 1px solid #cbd5e1;
  position: absolute;
  opacity: 0;
  display: none;
}
.plan .plan-content {
  padding: 1rem 1.4rem;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 0.1rem solid var(--gray-300, #d0d5dd);
  border-radius: 6px;
  -webkit-transition: -webkit-box-shadow 0.4s;
  transition: -webkit-box-shadow 0.4s;
  -o-transition: box-shadow 0.4s;
  transition: box-shadow 0.4s;
  transition: box-shadow 0.4s, -webkit-box-shadow 0.4s;
  position: relative;
}
.plan-details {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
  transition: ease-in 0.3s;
}
.plan-details p {
  color: #667085;
  font-size: 1.6rem;
  font-weight: 400;
}

.plan input[type="radio"]:checked + .plan-content .plan-details p,
.plan input[type="checkbox"]:checked + .plan-content .plan-details p {
  color: #ffff;
}
.plan input[type="checkbox"]:checked + .plan-content,
.plan input[type="radio"]:checked + .plan-content {
  border: 1px solid #cbd5e1;
  background: #cbd5e1;
  -webkit-transition: ease-in 0.3s;
  -o-transition: ease-in 0.3s;
  transition: ease-in 0.3s;
}
.plan-checked-icon {
  width: 16px;
  height: 16px;
  border-radius: 4px;
  border: 1px solid #aaa;

  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  margin-right: 9px;
}
.plan-checked-icon .check_icon {
  display: none;
}
.plan
  input[type="radio"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon,
.plan
  input[type="checkbox"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon {
  border: 1px solid #5c5c5c;
}
.plan
  input[type="radio"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon
  .check_icon,
.plan
  input[type="checkbox"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon
  .check_icon {
  display: block;
}
.plan.plan-button {
  width: inherit;
  display: block ruby;
  margin: 12px 0 !important;
}

.plan-button-colors input[type="checkbox"]:checked + .plan-content,
.plan-button-colors input[type="radio"]:checked + .plan-content {
  background: #2e2e2e !important;
}

.plan-button-colors input[type="radio"]:checked + .plan-content .plan-details p,
.plan-button-colors
  input[type="checkbox"]:checked
  + .plan-content
  .plan-details
  p {
  color: #fff !important;
}

.plan-button-colors
  input[type="radio"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon,
.plan-button-colors
  input[type="checkbox"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon {
  border: 1px solid #fff;
}

.plan-button-colors
  input[type="radio"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon
  .check_icon,
.plan-button-colors
  input[type="checkbox"]:checked
  + .plan-content
  .plan-details
  .plan-checked-icon
  .check_icon {
  color: #fff;
}

.plan-checked-icon .check_icon svg {
  display: inherit;
}
.check_icon {
  font-size: 14px;
}

